body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav .brand-logo{
  font-family: cursive;
  font-weight: bold;
}
ul.notifications li span{
  display:block;
}
ul.notifications li span.note-date{
  text-align:right;
}
.card-title{
  font-weight: 400 !important;
}
ul.notifications li{
  padding:5px 10px 10px;
}
ul.notifications li:not(:last-child){
 border-bottom: solid 1px #a2a2a2;
}
.col .section{
  box-shadow:0 4px 10px 0px rgba(0,0,0,0.14), -4px 0 10px 0px rgba(0,0,0,0.14);
  margin:20px 0;
  padding:30px;
}
.project-list a:not(:last-child) .project-summary {
  border-bottom: solid 1px #a2a2a2;
}
